html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

textarea, pre, input {
  border: none;
  outline: none;
  padding: 0;
  font-family: Consolas, Menlo, Courier, monospace;
}

.scrolled-y {
  height: 100%;
  overflow-y: auto;
}

.container {
  height: 100%;
  width: 100%;
  grid-template: "editor tree" 1fr
                 "query query" 30px
                 "selector-ast output" 1fr
                 / 1fr 1fr;
  grid-auto-flow: row;
  gap: 0;
  display: grid;
}

.editor, .tree, .selector-ast, .output {
  border: 1px solid gray;
  overflow: hidden;
}

.editor {
  grid-area: editor;
}

.tree {
  grid-area: tree;
}

.query {
  grid-area: query;
}

.selector-ast {
  grid-area: selector-ast;
}

.output {
  grid-area: output;
}

.source, .source-tree {
  width: 100%;
  height: 100%;
  resize: none;
}

.input {
  height: 30px;
  width: 100%;
  padding: 0 10px;
}

